
import axios from 'axios';

var navLanguage =  navigator.language.toLowerCase();
var languages = ['zh','zh-cn','cn'];
var language;
language = languages.includes(navLanguage) ? 'zh' : 'en';

function getAxois(url,method,perms) {
    return  axios({
        method:method,
        url:url,
        data:perms,

    })
}

function getNodeAxois(url,method,perms) {
    return  axios({
        method:method,
        url:url,
        data:perms,
    })
}

//API
